import React from 'react'
import './Statistic.scss'

function Statistic() {
    const [active, setActive] = React.useState(1)

    function handleActive(index) {
        setActive(index)
    }
    return (
        <div className='__container'>
            <div className="statistic">
                <div className="tabs">
                    <p className='tabs__label'> :</p>
                    <div className="tabs__list">
                        <div className={`tabs__tab ${active === 1 ? "tabs__active" : ""}`} onClick={() => handleActive(1)}></div>
                        <div className={`tabs__tab ${active === 2 ? "tabs__active" : ""}`} onClick={() => handleActive(2)}></div>
                        <div className={`tabs__tab ${active === 3 ? "tabs__active" : ""}`} onClick={() => handleActive(3)}></div>
                        <div className={`tabs__tab ${active === 4 ? "tabs__active" : ""}`} onClick={() => handleActive(4)}></div>
                        <div className={`tabs__tab ${active === 5 ? "tabs__active" : ""}`} onClick={() => handleActive(5)}></div>
                    </div>
                </div>
                <div className="content-tabs">
                    <div className={`content ${active === 1 ? "content__active" : ""}`}>
                          : 107
                    </div>
                    <div className={`content ${active === 2 ? "content__active" : ""}`}>
                          : 107
                    </div>
                    <div className={`content ${active === 3 ? "content__active" : ""}`}>
                          : 107
                    </div>
                    <div className={`content ${active === 4 ? "content__active" : ""}`}>
                          : 107
                    </div>
                    <div className={`content ${active === 5 ? "content__active" : ""}`}>
                          : 107
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Statistic